.Card {
  width: 96vw;
  min-height: 90vh;
  height: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  border-radius: 2vw;
  background-color: #4e3644;
  color : #ccb5c8;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

  margin: 5vh 2vw;

  display: flex;
  position: relative;
}
